* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.about_body {
    width: 1400px;
    height: fit-content;
    margin-top: 100px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;

}

.about_heroImg {
    background-image: url("../../assets/zdjecia/banner2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    margin: 25px;
    width: 1350px;
    height: 200px;
    margin-bottom: 40px;

}

.about_heroTitle {
    font-size: 30px;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    margin-bottom: 40px;

}

.about_heroP {
    color: #fff;
    text-align: center;
    font-size: 20px;
    width: 600px;
    margin-bottom: 40px;
}

.about_banner {
    background-image: url("../../assets/zdjecia/mycie1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    margin: 25px;
    width: 800px;
    height: 400px;
    margin-bottom: 40px;
}
.about_text{
    color: #fff;
    font-size: 20px;
    width: 1000px;
    text-align: center;
    margin-bottom: 100px;
}

.about_container-1 {
    width: 700px;
    height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 20px;
    margin-bottom: 40px;
    border: 1px solid #fff;
    border-radius: 10px;
    margin-left: -300px;
}

.about_img-1 {
    background-image: url("../../assets/zdjecia/mycie2.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
    width: 300px;
    height: 300px;
}

.about_p {
    color: #fff;
    font-size: 30px;
    width: 300px;
    text-align: center;
    padding: 10px;
}

