* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.contact_body {
    width: 1400px;
    height: 600px;
    margin-top: 100px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:50px;
}
.contact_gap{
    height:400px;
    width: 4px;
    background:white;
}
.info_body{
    border:3px solid #fff;
    border-radius:16px;
    
    width:600px;
    height:500px;
    display: flex;
    justify-content: center;
    align-items: center;

}
.info_body .phone p{
padding:10px;
/* border:solid 1px red; */
margin-bottom:20px;
font-weight: 800;
font-size:20px;
color:rgba(2500, 255, 255, 0.7);
text-align: center;
transition:0.4s;
cursor: pointer;
}
.info_body .phone p:hover{
    color:#fff;
    scale:1.1;

    }
.info_body .phone p:last-child{
    margin-bottom:0;
}
.map_body{
    width:600px;
    height:500px;
    border:3px solid #fff;
    border-radius:16px;
    overflow: hidden;
}
.map{
    width: 100%;
    height: 100%;
    background-image:url(../../assets/zdjecia/google_maps.png);
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    transition:0.5s;
    cursor: pointer;
    
}
.map:hover{
    box-shadow: inset 0 0 20px #fff;
}
.card{
    width:460px;
    height:340px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.6);
    border:2px solid var(--clr-primary);
    overflow:hidden;
    border-radius:12px;
    transition: 0.6s;
    cursor: pointer;
    
}
.card:hover{
    box-shadow: 0 0 20px var(--clr-primary);
}
.gap_line{
    width: 496px;
    height:4px;
    background:rgb(111, 131, 118);
}
.top{
    height:120px;
    padding: 40px;
    font-size:20px;
    color:#fff;
    background:rgb(9, 67, 105);
}
.top p{
    margin-bottom:10px;
}
.bottom{
    background:#fff;
    height: 216px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size:17px;
    font-weight:bold;
    color:rgb(4, 4, 99);
}
.bottom span{
    font-size:20px;
    margin-bottom:10px;
}
.img_logo{
    width: 350px;
}
.bottom_info{
    margin-left:-80px;
}
.bottom_info a{
    color:rgb(4, 4, 99);
    transition:0.4s;
}
.bottom_info a:hover{
    color:var(--clr-primary);
}
