.home__body {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.home__title {

    margin-top: 48px;
    font-size: 40px;
    color: rgb(241, 241, 241);
    z-index: 1;
    text-align: center;
}

.home__title span {
    color: var(--clr-primary)
}

.home__tile{
    width: 1000px;
    height: 400px;
    margin-top: 120px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    border: 1px solid rgba(0, 0, 0, 0.325);
    border-radius: 10px;
    padding:20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap:20px;
}
.home__tile:last-child(){
    margin-bottom:40px;
}

.tile__img{
   width:400px;
   height:350px; 
   border-radius:12px;
}
.tile__content{
    width: 500px;
    height:350px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.tile__title{
    color:#fff;
    font-size:34px;
}
.tile__line{
    width: 350px;
    margin-top:20px;
    margin-bottom:20px;
    height:2px;
    background:#fff;
}
.tile__text{
    color:#fff;
    letter-spacing: 0.9px;
    text-align: center;
}
.home__tile ul{
    margin-top:50px;
    display: flex;
    gap:40px;
}
.home__tile li{
    color:var(--clr-primary);
    font-weight: 800;
    letter-spacing: 0.9px;
}