.tile__container{
    width: 1700px;
    height: 400px;
    margin-top: 100px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    box-shadow: 10px 20px 40px rgba(0, 0, 0, 0.6);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    position:relative;


}
.tile__title{
    color:var(--clr-primary);
    font-size:50px;
    margin-bottom:20px;
}
.tile__img--main{
    background-image:url("../../assets/zdjecia/banner1.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size:cover;
    width: 1500px;
    border-radius:12px;
    height: 100px;
    margin-bottom:20px;
}
.tile__container p{
    color:#fff;
    text-align: center;
    font-size:20px;
    padding:0 400px;
}


.tile__body{
    width: 700px;
    height: 300px;
    margin-top: 200px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:10px;
    padding:20px;
    position:absolute;
    transition:0.3s;
}
