.tile__body--wide{
    width: 1000px;
    height: 450px;
    margin-top: 200px;
    background: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(3px);
    backdrop-filter: blur(3px);
    border-radius: 10px;
    position:absolute;
    top:1400px;
    left:400px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    transition:0.3s;
}
.images{
    display: flex;
    justify-content: center;
    gap:20px;
    padding:20px;
}
.images .i-1{
    background-image:url("../../assets/zdjecia/wartung14.jpg");
    background-repeat: no-repeat;
    background-position: 0px -250px;
    background-size:100%;
}
.images .i-2{
    background-image:url("../../assets/zdjecia/wartung16.jpg");
    background-repeat: no-repeat;
    background-position: 0px -200px;
    background-size:100%;
}
.images .i-3{
    background-image:url("../../assets/zdjecia/wartung18.jpg");
    background-repeat: no-repeat;
    background-position: 0px -250px;
    background-size:100%;
}
.t-1{
    top:600px;
    left:200px;
}
.t-2{
    top:600px;
    left:1000px;
}
.t-3{
    top:1000px;
    left:200px;
}
.t-4{
    top:1000px;
    left:1000px;
}
.t-6{
    top:1950px;
