.tile__spacer{
    width: 2px;
    height:200px;
    background:#fff;
}
.tile__spacer--wide{
    width: 800px;
    height:2px;
    background:#fff; 
}
.tile__body p{
    color:#fff;
    font-size:20px;
    text-align: center;
    width: 400px;
}
.tile__body--wide p{
    padding:20px 100px;
    color:#fff;
    font-size:20px;
    text-align: center;
    width: 1000px;
}
